<template>
    <div class="text-pointer" @click="$router.push('/views/users/edit/' + user.id)">

        <div class="text-center">
          <v-avatar size="20" v-if="user.avatar_url">
            <img
              :src="
                this.$store.state.auth.baseURL +
                '/public/w90/' +
                user.avatar_url
              "
              :alt="user.username"
            />
          </v-avatar>

          <v-avatar v-else size="20" color="white">
            <v-icon size="20"> mdi-account-circle </v-icon>
          </v-avatar>
        </div>
      
        <div class="font-weight-light">
          <span>{{ user.username }}</span>
        </div>

    </div>
</template>

<script>
  export default {
    name: 'UserSmallComponent',

    props: {
      user: {
        type: Object,
        required: true
      }
    },
    data: () => ({
    }),
  }
